

.Pokedex {
  /* background-color: #171717f9; */
  color: #EFF0F7;
  display: grid;
  grid-template-rows: 10rem -webkit-min-content;
  grid-template-rows: 10rem min-content;
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(10, [col-start] minmax(-webkit-min-content, 11rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(10, [col-start] minmax(min-content, 11rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  row-gap: 3.5rem; }
  @media only screen and (min-width: 112.5em) {
    .Pokedex {
      grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(10, [col-start] minmax(-webkit-min-content, 15rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
      grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(10, [col-start] minmax(min-content, 15rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end]; } }
  @media only screen and (max-width: 65.62em) {
    .Pokedex {
      grid-template-columns: [full-start] minmax(3rem, 1fr) [center-start] repeat(10, [col-start] minmax(-webkit-min-content, 11rem) [col-end]) [center-end] minmax(3rem, 1fr) [full-end];
      grid-template-columns: [full-start] minmax(3rem, 1fr) [center-start] repeat(10, [col-start] minmax(min-content, 11rem) [col-end]) [center-end] minmax(3rem, 1fr) [full-end];
      grid-template-rows: 20rem -webkit-min-content;
      grid-template-rows: 20rem min-content; } }
  @media only screen and (max-width: 37.5em) {
    .Pokedex {
      grid-template-rows: -webkit-min-content min-content;
      grid-template-rows: min-content min-content; } }
  @media only screen and (max-width: 25em) {
    .Pokedex {
      grid-template-columns: [full-start] minmax(1rem, 1fr) [center-start] repeat(10, [col-start] minmax(-webkit-min-content, 11rem) [col-end]) [center-end] minmax(1rem, 1fr) [full-end];
      grid-template-columns: [full-start] minmax(1rem, 1fr) [center-start] repeat(10, [col-start] minmax(min-content, 11rem) [col-end]) [center-end] minmax(1rem, 1fr) [full-end]; } }

  .Pokedex--light {
    /* -color: #171717f9;
    color: #EFF0F7; */
  
  }







.Main-content {
  position: relative;
  grid-column: center-start / center-end;
