.fullCard {
    
    height: 100vh;
    width: 100vw;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, .95);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    cursor: -webkit-zoom-out;
    cursor: zoom-out;
    /* cursor:default; */
    z-index: 500;
}

.fullCard h2 {
    display: inline-block;
    /* font-size: 50vw !important; */
}

.fullCard p {
    text-align: center;
    margin-top: 2rem;
    padding: 0 1rem;
}

.fullCard svg {
    cursor: pointer;
    fill: white;
    stroke: white;
    position: fixed;
    top: 2.5rem;
    left: 3rem;
    width: 4rem;
    height: 4rem;
    /* top */
    z-index: 999;
}

.fullCard__img {
    position: relative;
    height: 75%;
    width: 90vw;
    /* width: 100%; */
    /* aspect-ratio: 16/9; */
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

    opacity: 0;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

@media (max-width: 37.5em) {
    .fullCard h2 {
        font-size: 2.5rem !important;
    }
}

@media (max-width: 28.12em) {
    .fullCard svg {
        left: 3%;
    }
    .fullCard__img {
        height: 45rem;
    }
}

@media (max-width: 22.5em) {
    .fullCard__img {
        height: 40rem;
    }
    .fullCard svg {
        left: 2%;
    }
}



