.loader {
    position: fixed;
    margin-top: 5rem;
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    /* z-index: ; */
}

.loader svg {
    /* stroke: #27FB6B; */
    fill: none;
    height: 7rem;
    width: 7rem;
}

.loader svg circle {
    -webkit-animation: 4s a infinite linear, 3s o infinite linear;
            animation: 4s a infinite linear, 3s o infinite linear;
}

@-webkit-keyframes a {
    from {
        stroke-dasharray: 100 0
    }
    50% {
        stroke-dasharray: 0 100
    }
    to {
        stroke-dasharray: 100 0
    }
}

@keyframes a {
    from {
        stroke-dasharray: 100 0
    }
    50% {
        stroke-dasharray: 0 100
    }
    to {
        stroke-dasharray: 100 0
    }
}

@-webkit-keyframes o {
    from {
        stroke-dashoffset: 75
    }
    to {
        stroke-dashoffset: 375
    }
}

@keyframes o {
    from {
        stroke-dashoffset: 75
    }
    to {
        stroke-dashoffset: 375
    }
}



